






























































var _self;
export default {
	data() {
		return {
			follows: {
				avatar: '',
				nickname: '',
				toUId: ''
			},
			fans: {
				uId: 0,
				avatar: '',
				nickname: '',
				fromUId: ''
			},
			/* fans: [], */
			/* follows: [], */
			likes: [],
			userId: uni.getStorageSync('login_key').userId,
			followed: true,
			recommend: true,
			special: false,
			serialize: false
		}
	},
	onLoad: function() {
		_self = this;
			uni.setNavigationBarTitle({
			title: '我的关注'
		});
	},
	onShow: function() {
		this.getFollows();
		this.getFans();
		this.getlikes();
	},
	methods: {
		clickshow: function() {
			this.recommend = true;
			this.special = false;
			this.serialize = false;
		},
		clickshow2: function() {
			this.recommend = false;
			this.special = true;
			this.serialize = false;
		},
		clickshow3: function() {
			this.recommend = false;
			this.special = false;
			this.serialize = true;
		},
		getFollows: function() {
			var _this = this;
			uni.request({
				url: this.apiServer + '/follow/followlist?fromUId=' + uni.getStorageSync('login_key').userId,
				method: 'GET',
				header: {
					'content-type': 'application/json'
				},
				success: res => {
					_this.follows = res.data.data;
				}
			});
		},
		getFans: function() {
			var _this = this;
			uni.request({
				url: this.apiServer + '/follow/fanlist?toUId=' + uni.getStorageSync('login_key').userId,
				method: 'GET',
				header: {
					'content-type': 'application/json'
				},
				success: res => {
					_this.fans = res.data.data;
				}
			});
		},
		getlikes: function() {
			var _this = this;
			uni.request({
				url: this.apiServer + '/like/likelist?fromUId=' + uni.getStorageSync('login_key').userId,
				method: 'GET',
				header: {
					'content-type': 'application/json'
				},
				success: res => {
					_this.likes = res.data.data;
				}
			});
		},
		gotoDetail: function(aId) {
			uni.navigateTo({
				url: '../article_detail/article_detail?aId=' + aId
			});

		},
		handleTime: function(date) {
			var d = new Date(date);
			var year = d.getFullYear();
			var month = d.getMonth() + 1;
			var day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate();
			var hour = d.getHours() < 10 ? '0' + d.getHours() : '' + d.getHours();
			var minutes = d.getMinutes() < 10 ? '0' + d.getMinutes() : '' + d.getMinutes();
			var seconds = d.getSeconds() < 10 ? '0' + d.getSeconds() : '' + d.getSeconds();
			return year + '-' + month + '-' + day + ' ' + hour + ':' + minutes + ':' + seconds;
		},
		cancelFollow: function() {
			uni.request({
				url: this.apiServer + '/follow/cancel',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					fromUId: uni.getStorageSync('login_key').userId,
					toUId: this.follows.toUId,
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showToast({
							title: '已取消关注'
						});
						this.getFollows();
					}
				}
			});
		}

	},

}
